"use client";

import { useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import { Switch } from "@/components/ui/switch";
import { Separator } from "@/components/ui/separator";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Bell, Mail, MessageSquare, Calendar, Users, Briefcase, AlertCircle, Smartphone } from "lucide-react";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";

interface NotificationPreference {
  id: string;
  title: string;
  description: string;
  email: boolean;
  sms: boolean;
  icon: any;
}

export default function NotificationsSettingsPage() {
  const [preferences, setPreferences] = useState<NotificationPreference[]>([
    {
      id: 'bookings',
      title: 'Booking Notifications',
      description: 'Get notified about new bookings, confirmations, and cancellations',
      email: true,
      sms: false,
      icon: Calendar,
    },
    {
      id: 'providers',
      title: 'Provider Updates',
      description: 'Notifications about provider registrations, completions, and status changes',
      email: true,
      sms: false,
      icon: Briefcase,
    },
    {
      id: 'customers',
      title: 'Customer Activity',
      description: 'Updates about customer registrations, inquiries, and feedback',
      email: true,
      sms: false,
      icon: Users,
    },
    {
      id: 'payments',
      title: 'Payment Notifications',
      description: 'Alerts for successful payments, refunds, and payment issues',
      email: true,
      sms: true,
      icon: AlertCircle,
    },
    {
      id: 'system',
      title: 'System Alerts',
      description: 'Important system updates, maintenance, and security notifications',
      email: true,
      sms: false,
      icon: Bell,
    },
  ]);

  const [emailFrequency, setEmailFrequency] = useState('instant');
  const [quietHours, setQuietHours] = useState(false);

  const updatePreference = (id: string, channel: 'email' | 'sms', value: boolean) => {
    setPreferences(prev => prev.map(pref => 
      pref.id === id ? { ...pref, [channel]: value } : pref
    ));
  };

  const handleSave = () => {
    console.log('Saving notification preferences:', preferences);
  };

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <div className="flex items-center gap-2">
            <Bell className="h-5 w-5 text-primary" />
            <CardTitle>Notification Settings</CardTitle>
          </div>
          <CardDescription>
            Configure how and when you receive notifications
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          {/* General Settings */}
          <div className="space-y-4">
            <h3 className="text-sm font-semibold">General Preferences</h3>
            
            <div className="flex items-center justify-between p-4 rounded-lg border bg-muted/50">
              <div className="space-y-0.5">
                <Label htmlFor="email-frequency" className="text-sm font-medium">
                  Email Digest Frequency
                </Label>
                <p className="text-xs text-muted-foreground">
                  How often to receive email summaries
                </p>
              </div>
              <Select value={emailFrequency} onValueChange={setEmailFrequency}>
                <SelectTrigger id="email-frequency" className="w-[180px]">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="instant">Instant</SelectItem>
                  <SelectItem value="hourly">Hourly</SelectItem>
                  <SelectItem value="daily">Daily Digest</SelectItem>
                  <SelectItem value="weekly">Weekly Summary</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="flex items-center justify-between p-4 rounded-lg border bg-muted/50">
              <div className="space-y-0.5">
                <Label htmlFor="quiet-hours" className="text-sm font-medium">
                  Quiet Hours (10 PM - 8 AM)
                </Label>
                <p className="text-xs text-muted-foreground">
                  Pause non-urgent notifications during quiet hours
                </p>
              </div>
              <Switch
                id="quiet-hours"
                checked={quietHours}
                onCheckedChange={setQuietHours}
              />
            </div>
          </div>

          <Separator />

          {/* Notification Channels */}
          <div className="space-y-4">
            <div>
              <h3 className="text-sm font-semibold mb-1">Notification Channels</h3>
              <p className="text-xs text-muted-foreground">
                Choose how you want to receive different types of notifications
              </p>
            </div>

            <div className="rounded-lg border">
              <div className="grid grid-cols-3 gap-4 p-4 bg-muted/30 border-b">
                <div className="col-span-1">
                  <p className="text-xs font-medium text-muted-foreground">Category</p>
                </div>
                <div className="flex items-center justify-center">
                  <Mail className="h-4 w-4 text-muted-foreground" />
                  <p className="text-xs font-medium text-muted-foreground ml-2">Email</p>
                </div>
                <div className="flex items-center justify-center">
                  <MessageSquare className="h-4 w-4 text-muted-foreground" />
                  <p className="text-xs font-medium text-muted-foreground ml-2">SMS</p>
                </div>
              </div>

              {preferences.map((pref, index) => {
                const Icon = pref.icon;
                return (
                  <div
                    key={pref.id}
                    className={`grid grid-cols-3 gap-4 p-4 items-center ${
                      index !== preferences.length - 1 ? 'border-b' : ''
                    }`}
                  >
                    <div className="col-span-1 flex items-start gap-3">
                      <div className="flex h-8 w-8 items-center justify-center rounded-full bg-cyan-100">
                        <Icon className="h-4 w-4 text-cyan-600" />
                      </div>
                      <div>
                        <p className="text-sm font-medium">{pref.title}</p>
                        <p className="text-xs text-muted-foreground">{pref.description}</p>
                      </div>
                    </div>
                    <div className="flex items-center justify-center">
                      <Switch
                        checked={pref.email}
                        onCheckedChange={(value) => updatePreference(pref.id, 'email', value)}
                      />
                    </div>
                    <div className="flex items-center justify-center">
                      <Switch
                        checked={pref.sms}
                        onCheckedChange={(value) => updatePreference(pref.id, 'sms', value)}
                      />
                    </div>
                  </div>
                );
              })}
            </div>
          </div>

          <Separator />

          {/* Save Button */}
          <div className="flex justify-end">
            <Button 
              onClick={handleSave}
              className="bg-gradient-to-r from-cyan-500 to-cyan-600 hover:from-cyan-600 hover:to-cyan-700"
            >
              Save Preferences
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
